<template>
  <base-facility>
    <template v-slot:title><router-link to="/facility" class="grey--text text-decoration-none">Kyoto</router-link> / No smoking</template>
    <template v-slot:default>
      <v-card class="v-card-responsive-table pa-5" shaped tile >
        <v-card-title class="grey--text mt-n5" v-once>
          {{ facilityTitle }} > {{ roomTypeTitle }} > No smoking
        </v-card-title>
        <v-simple-table class="custom-table d-flex mt-n4">
          <template v-slot:default>
            <thead>
            <tr>
              <th class="text-left">
                Room ID
              </th>
              <th class="text-left">
                Room name
              </th>
              <th class="text-left">
                Edit room
              </th>
            </tr>
            </thead>
            <tbody>
            <tr
              v-for="item in rooms"
              :key="item.id"
            >
              <td>{{ item.id }}</td>
              <td class="font-weight-medium">{{ item.name }}</td>
              <td class="text-center">
                <v-btn
                  color="white"
                  elevation="2"
                  x-small
                  fab
                  @click="editRoom"
                >
                  <v-icon color="primary">
                    mdi-pencil
                  </v-icon>
                </v-btn>
              </td>
            </tr>
            </tbody>
          </template>
        </v-simple-table>
        <div class="text-end mt-5">
          <create-room-modal>Create a new room</create-room-modal>
        </div>
      </v-card>
    </template>
  </base-facility>
</template>

<script>
import BaseFacility from '@/components/Facilities/BaseFacility'
import CreateRoomModal from '@/components/Facilities/Rooms/CreateModal'

export default {
  name: 'FacilityRoomsSettings',
  data () {
    return {
      facilityTitle: 'Facility list',
      roomTypeTitle: 'Kyoto',
      rooms: []
    }
  },
  mounted () {
    this.initRooms()
  },
  methods: {
    initRooms () {
      for (let i = 0; i < 5; i++) {
        this.rooms.push({
          id: Math.floor(Math.random() * 90000) + 10000,
          name: '10' + (i + 1)
        })
      }
    },
    editRoom () {
      this.$router.push({ path: '/facility/settings/43224/room-types/43323/rooms/25345/edit' })
    }
  },
  components: {
    BaseFacility,
    CreateRoomModal
  }
}
</script>

<style scoped lang="scss">

</style>
